/*
 * meta
 * @scrollTop: truefalse
 * @title: header title
 * @isHideHeader: 
 * @needLogin: 
 */
import Vue from 'vue'
import Router from 'vue-router'
import Storage from '@/utils/Storage'
import store from '@/store'

import Index from '@/pages/home/index'
import Home from '@/pages/home/components/home'
import Message from '@/pages/home/components/message'
// 
import Account from '@/pages/home/components/account'
// 
import Combo from '@/pages/combo'
// 
import PersonInfo from '@/pages/personInfo'
// 
import UserDeal from '@/pages/userDeal'
// 
import Like from '@/pages/personList/like'
// 
import LikeMe from '@/pages/personList/likeMe'
// 
import Follow from '@/pages/personList/follow'
// 
import Ask from '@/pages/personList/ask'
// 
import MessageDetail from '@/pages/messageDetail'
// 
import CustomerService from '@/pages/customerService'
// 
import Setting from '@/pages/setting'
// 
import BaseInfo from '@/pages/baseInfo'
// 
import SetUserData from '@/pages/setUserData'
// 
import Transcation from '@/pages/setting/transcation'
// 
import LikeDetail from '@/pages/setting/likeDetail'
// 
import UploadCert from '@/pages/Auth/uploadCert'
// 
import UploadEducation from '@/pages/Auth/uploadEducation'
// 
import UploadCareer from '@/pages/Auth/uploadCareer'
// 404
import NotFound from '@/pages/NotFound'
// 
import Test from '@/pages/test'

Vue.use(Router)

let router = new Router({
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    let position = null
    // 
    if (savedPosition) {
      position = savedPosition
    } else {
      // meta
      // 
      if (to.meta.scrollTop === false) {
        position = { x: 0, y: to.meta.scrollHeight }
      } else {
        position = { x: 0, y: 0 }
      }
    }
    return position
  },
  routes: [
    {
      name: '',
      path: '',
      component: Index,
      children: [
        {
          name: 'Home',
          path: '/',
          alias: '/index.html',
          component: Home,
          meta: {
            isHideHeader: true
          }
        },
        {
          name: 'Messages',
          path: '/messages.html',
          component: Message,
          meta: {
            needLogin: true
          }
        },
        {
          name: 'Account',
          path: '/account.html',
          component: Account,
          meta: {
            isHideHeader: true
          }
        }
      ]
    },
    {
      name: 'combo',
      path: '/combo.html',
      component: Combo,
      meta: {
        title: ''
      }
    },
    {
      name: 'PersonInfo',
      path: '/info-:id.html',
      component: PersonInfo,
      meta: {
        scrollTop: true
      }
    },
    {
      name: 'UserDeal',
      path: '/userDeal.html',
      component: UserDeal
    },
    {
      name: 'Like',
      path: '/like.html',
      component: Like,
      meta: {
        title: ''
      }
    },
    {
      name: 'LikeMe',
      path: '/likeme.html',
      component: LikeMe,
      meta: {
        title: ''
      }
    },
    {
      name: 'Follow',
      path: '/follow.html',
      component: Follow,
      meta: {
        title: ''
      }
    },
    {
      name: 'Ask',
      path: '/ask.html',
      component: Ask,
      meta: {
        title: ''
      }
    },
    {
      name: 'MessageDetail',
      path: '/message-detail-:id.html',
      component: MessageDetail
    },
    {
      name: 'Setting',
      path: '/setting.html',
      component: Setting,
      meta: {
        title: ''
      }
    },
    {
      name: 'CustomerService',
      path: '/customer-service.html',
      component: CustomerService,
      meta: {
        title: ''
      }
    },
    {
      name: 'SetUserData',
      path: '/setUserData.html',
      component: SetUserData,
      meta: {
        isHideHeader: true,
        needLogin: true
      }
    },
    {
      name: 'BaseInfo',
      path: '/baseInfo.html',
      component: BaseInfo,
      meta: {
        title: '',
        needLogin: true
      }
    },
    {
      name: 'Transcation',
      path: '/transcation.html',
      component: Transcation,
      meta: {
        title: ''
      }
    },
    {
      name: 'LikeDetail',
      path: '/likeDetail.html',
      component: LikeDetail,
      meta: {
        title: ''
      }
    },
    {
      name: 'UploadCert',
      path: '/upload-cert.html',
      component: UploadCert,
      meta: {
        title: ''
      }
    },
    {
      name: 'UploadEducation',
      path: '/upload-education.html',
      component: UploadEducation,
      meta: {
        title: ''
      }
    },
    {
      name: 'UploadCareer',
      path: '/upload-career.html',
      component: UploadCareer,
      meta: {
        title: ''
      }
    },
    {
      name: 'Test',
      path: '/test.html',
      component: Test,
      meta: {
        title: 'cropper'
      }
    },
    {
      name: 'NotFound',
      path: '*',
      component: NotFound
    }
  ]
})

router.beforeEach((to, from, next) => {
  // 
  if (from.meta.scrollTop === false) {
    const scrollHeight = window.pageYOffset || document.documentElement.scrollTop
    from.meta.scrollHeight = scrollHeight
  }
  if (store.state.userData.currentMemberId) {
    next()
  } else {
    if (Storage.load('userId')) {
      if (to.meta.needLogin) {
        store._actions.fetchUserData[0]({ currentMemberId: Storage.load('userId') }).then(res => {
          next()
        }, () => {
          next({
            path: '/account.html',
            query: {
              direct: to.fullPath
            }
          })
        })
      } else {
        store._actions.fetchUserData[0]({ currentMemberId: Storage.load('userId') })
        next()
      }
    } else {
      if (to.meta.needLogin) {
        next({
          path: '/account.html',
          query: {
            direct: to.fullPath
          }
        })
      } else {
        next()
      }
    }
  }
})

export default router
